












































.error_item_wrap{
	&__item{
		position: absolute;
		padding: 6px 12px;
		background: #d1404a;
		color: #fff;
		border-radius: 4px;
		top: 10px;
		left: -35%;
		font-size: 0.8em;
		display: flex;
		justify-content: center;
		align-items: center;
		
		.icon_right {
			position: relative;
			right: -20px;
			color: #d1404a;
			font-size: 1em;
		}
	}
}



