












































$borderStep: #ddd;
$textStep: #999;
$successColor: #34c263;
$successColorText: #fff;

.order_app {
	min-height: 450px;
	
	&__steps{
		margin-bottom: 25px;
		
		.el-step__head.is-process {
			color: $textStep;
			border-color: $borderStep;
		}
		
		.el-step.is-horizontal .el-step__line {
			height: 4px;
			background: $borderStep;
		}
		.el-step__line-inner {
			border-color: $successColor;
			background: $successColor;
			height: 4px;
		}
		.el-step__head.is-finish {
			color: $successColor;
			border-color: $successColor;
			
			.el-step__icon {
				background: $successColor;
				color: $successColorText;
			}
		}
		
		.el-step__icon {
			height: 30px;
			width: 30px;
			
			&.is-text {
				top: -2px;
			}
		}
		
		
		
	}
	
	&__actions {
	
	}
}



