












































































































































.step-3 {
	&__info{
		display: flex;
		flex-direction: column;
		min-height: 160px;
		margin: 30px 0;
		
		.address_block {
			font-size: 16px;
			line-height: 1.5rem;
			flex: 7;
			margin-bottom: 25px;
		}
	}
	
	.sum_product, .sum_delivery {
		font-size: 16px;
		flex: 2;
	}
	
	.sum_delivery {
		flex: 10;
	}
	.info_price {
		font-size: 0.9em;
	}
	.info_title {
		font-size: 1.4em;
	}
	
	.sum_total {
		.info_title {
			font-size: 1.2em;
		}
		
		.info_price {
			font-size: 1.4em;
		}
	}
	
	.check__cont {
		display: flex;
		flex-direction: column;
	}
	
	.check__sm {
		font-size: 16px;
	}
}



