















































































































































































































































































































$colorField: #808080;
$bgInput: #f0efec;

.error_input input {
	border:1px solid #d1404a !important;
}

.step-1 {
	padding-left: 15px;
	
	&__info_block {
		text-align: center;
		font-size: 0.8em;
		margin: 15px auto;
	}
	
	.star {
		color: red;
		font-weight: bold;
		font-style: normal;
		font-size: 1.4em;
	}
	
	.order1__radio label {
		margin-bottom: 5px;
	}
	
	&__row {
		.star {
			position: absolute;
			top: 20px;
			left: -7px;
			font-size: 1em;
		}
		
		.el-autocomplete{
			width: 100%;
		}
		
		.el-input__inner {
			width: 100%;
			background: $bgInput;
			padding: 15px 20px;
			-webkit-border-radius: 30px;
			border-radius: 30px;
			font-size: 18px;
			color: $colorField;
			height: 50px;
			border-color: $bgInput;
			
			&::placeholder{
				color: $colorField
			}
		}
	}
	
	&__row.cells {
		display: flex;
		justify-content: space-between;
		
		.cell {
			width: 48%;
		}
	}
	
	
}

.grey_select {
	width: 100%;
	background: $bgInput;
	color: $colorField;
	border-radius: 30px;
	
	&:hover .el-input__inner{
		border-radius: 30px;
		border-color: #e5e4e1;
	}
	
	.el-input__inner {
		border-color: transparent;
		background: transparent;
		height: 50px;
		color: $colorField;
		font-size: 18px;
	}
	
	.el-input__suffix-inner > .el-input__icon,
	.el-input__suffix-inner > .el-icon-circle-close {
		font-size: 18px !important;
		margin-right: 4px;
	}
	
	.el-input__inner[plaseholder]{
		color: $colorField;
	}
	.el-input.is-focus .el-input__inner {
		border-color: #e5e4e1;
		border-radius: 30px;
	}
}
.el-select-dropdown__item.selected .color_select_green {
	color: #71c54a
}

.tip_hover {
	background-image: url(/local/dist/images/sprite2.png);
	background-position: -182px -529px;
	width: 16px;
	height: 16px;
	display: inline-block;
	position: absolute;
	top: 17px;
	right: 10px;
	cursor: pointer;
	
	.tip_field {
		display: none;
		position: absolute;
		width: 300px;
		right: -25px;
		top: 33px;
		line-height: 20px;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0.18);
		font-size: 13px;
		z-index: 1;
		background: #fff;
		padding: 20px 30px;
	}
	
	&:hover .tip_field {
		display: block;
		
		&:before {
			content: '';
			border-bottom: 15px solid #fff;
			border-left: 15px solid transparent;
			border-right: 15px solid transparent;
			top: -15px;
			position: absolute;
			right: 23px;
		}
	}
}

.order_modals .modal_content {
	justify-content: center;
	align-items: center;
	position: fixed;
	
	.order_modal_inner {
		width: 430px;
		min-height: 510px;
		background: #fff;
		
		.error_modal {
			
			&__head {
				height: 245px;
				background: url(/local/dist/images/r_bottom_border.png) left bottom #ff0000 repeat-x;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
		
		&__content {
			min-height: 150px;
			padding: 25px;
			margin: 0;
			text-align: left;
			font-size: 95%;
			line-height: 20px;
			float: none;
			font-weight: 300;
			color: $colorField;
		}
		
		&__buttons {
			display: flex;
			justify-content: center;
			padding: 25px;
		}
		
		h2 {
			font-size: 36px;
			font-family: 'HelveticaNeueCyr-Thin', "Open Sans", Arial, "Helvetica Neue", Helvetica, sans-serif;
			margin: 10px 0 20px 0;
			padding: 10px 0 17px 0;
			border-bottom: 1px solid #e5e5e5;
			text-align: left;
		}
	}
	
}




