
































































































































































$greyBorder: 1px solid #e5e5e5;
$successColor: #34c263;
$successColorText: #fff;

.step-2{

	&__times {

	}

	&__time-shop-wrap {
		margin-bottom: 30px;

		&:hover .img_shop {
			box-shadow: 0 0 20px #c7c7c7;
			transition: 0.4s;
		}

		&:hover .shop_time_selected {
			color: $successColor;
			border-color: $successColor;
		}
	}


	.shop_time_selected {
		font-size: 12px;
		color: #373737;
		text-decoration: none;
		line-height: 16px;
	}

	.item_message {
		margin: 7px 0 25px 0;
		font-size: 12px;
		color: #898989;
		width: 250px;
		line-height: 20px;
	}
}

.time-info {
	display: flex;
	flex-direction: column;
}

.time-shop {
	display: flex;
}

.img_shop {
	width: 128px;
	height: 84px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: $greyBorder;
	margin-right: 20px;
	cursor: pointer;


	img {
		width: 80%;
		height: auto;
	}
}

.shop_name {
	font-size: 24px;
	color: #000;
	margin-bottom: 10px;
}

.calendar {
	background: #fff;
	min-height: 595px;
	width: 850px;
	position: absolute;
	top: 0;
	left: -215%;

	.time-shop {
		padding: 25px 20px 20px;
		align-items: center;

		.img_shop {
			margin-right: 30px;
		}
	}

	.el-tabs__nav {
		.el-tabs__item {
			font-size: 13px;
			color: #373737;
			line-height: 18px;
			text-align: center;
			height: 65px;
			border: none;
			border-radius: 0;
			padding-top: 10px;
		}

	}

	.el-tabs--card>.el-tabs__header .el-tabs__nav {
		border: none;
		border-radius: 0;
		top: 1px;
		left: 15px;
	}
	.el-tabs--card>.el-tabs__header .el-tabs__item.is-active {
		border: $greyBorder;
		border-bottom: 1px solid #fff;
	}

	.el-tabs__nav-wrap {
		padding: 0 30px;
	}
	.el-tabs__header {
		padding: 0;
		margin-bottom: 0;
	}

	.el-tabs__nav-next, .el-tabs__nav-prev {
		height: 65px;
		font-size: 20px;
		display: flex;
		align-items: center;
		width: 25px;

		&:hover {
			border: $greyBorder;
			border-bottom: none;
		}
	}

	.tab_item_body {
		.row_item {
			padding: 20px 30px;
			border-bottom: $greyBorder;
			display: flex;

			&:hover {
				background: #f0f0f0;
				cursor: pointer;
			}

			&__name {
				flex: 10;
			}
			&__price {
				flex: 16;
			}
			&__status {
				flex: 10;
				text-align: right;
				font-weight: bold;
				font-size: 15px;
			}
			&__status, &__status a {
				color: #3abb4c;
				text-decoration: none;
			}
		}

		.row_item.no_active {
			opacity: 0.5;

			&:hover {
				background: transparent;
				cursor: default;
			}
		}
	}
}

.error_time_valid {
	color: #d1404a;
	font-size: 13px;
}



