















































.main_modal_overlay {
	background: rgba(255,156,34,0.69);
	position: fixed;
	height: 100%;
	width: 100%;
	opacity: 0.8;
	z-index: 500;
	top: 0;
	left: 0;
	overflow: hidden; }

.body_overflow {
	overflow: hidden; }

.modal_content {
	position: absolute;
	top: 0;
	left: 0;
	max-width: 1600px;
	min-width: 1200px;
	height: 100vh;
	width: 100%;
	z-index: 501;
	display: flex;
	flex-wrap: nowrap; }





